.chat-componet {
    width: 300px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;

    .chat-header {
        display: flex;
        justify-content: space-around;
        padding: 10px;
        background-color: #f5f5f5;
        border-bottom: 1px solid #ddd;

        .button {
            font-size: 0.9rem;
            padding: 5px 10px;
            border: none;
            background-color: #eaeaea;
            cursor: pointer;
            transition: background-color 0.3s ease;

            &.is-active {
                background-color: #4a4a4a;
                color: #fff;
            }

            &:hover {
                background-color: #d4d4d4;
            }
        }
    }

    .chat-list {
        max-height: 400px;
        overflow-y: auto;
        padding: 10px;

        .chat-item {
            display: flex;
            align-items: center;
            padding: 8px;
            border-bottom: 1px solid #eee;

            &:hover {
                background-color: #f5f5f5;
            }

            a {
                display: flex;
                align-items: center;
                text-decoration: none;
                color: #333;
                width: 100%;

                .icon {
                    margin-right: 10px;
                    font-size: 1.5rem;
                    color: #4a4a4a;
                }

                .chat-name {
                    flex-grow: 1;
                }

                .chat-status {
                    width: 10px;
                    height: 10px;
                    border-radius: 50%;
                    margin-left: 10px;

                    &.is-online {
                        background-color: #4caf50;
                    }

                    &.is-offline {
                        background-color: #f44336;
                    }
                }
            }
        }
    }
}